.screen-reader-text {
  display: none;
}

[contenteditable="true"]:focus {
  outline: 1px solid #2e52ff;
  outline-offset: 5px;
}

body[data-udy-fe-enabled="link_"] a:after, body[data-udy-fe-enabled="iframe_"] iframe:before {
  content:'';
  position:absolute;
  top:-10px; bottom:-10px; 
  left:-10px; right:-10px; 
}
body[data-udy-fe-enabled="link_"] a  {
  position: relative;
}

body[data-udy-fe-enabled="iframe_"] iframe[data-udy-fe] {
  position: relative;
  pointer-events: none;
  cursor: pointer;
}

body[data-udy-fe-enabled="iframe_"] .udesly-hover-indicator {
  pointer-events: none;
}

.button.udesly-video-button, button.udesly-iframe-button {
  display: none;
}

body[data-udy-fe-enabled="video_"] button.udesly-video-button, body[data-udy-fe-enabled="iframe_"] button.udesly-iframe-button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color:#2e52ff;
  color: white;
  display: block;
}


